import React, { useState, useRef } from 'react';
import Alert from '../../alert';
import AlertIcon from '../../alert-icon';
import AlertLabel from '../../alert-label';
import AlertLabelTitle from '../../alert-label-title';
import ButtonGroup from '../../button-group';
import ButtonIcon from '../../button-icon';
import ButtonText from '../../button-text';
import Col from '../../col';
import Divider from '../../divider';
import Grid from '../../grid';
import Icon from '../../icon';
import Input from '../../input';
import InputOption from '../../input-option';
import List from '../../list';
import Row from '../../row';
import Typography from '../../typography';
import './style.scss';

const InputExample = () => {
  const inputTwoRef = useRef(null);
  const inputThreeRef = useRef(null);
  const inputFourRef = useRef(null);
  const inputSixRef = useRef(null);
  const inputSevenRef = useRef(null);

  const stateTwoDefaultHelpText = 'Type "peter" into the input field';
  const stateTwoDefaultErrorText = 'Error: Please enter "peter"';
  const stateSixDefaultHelpText = 'Type "john" into the input field';
  const stateSevenDefaultHelpText = 'Please enter your password';

  const [stateOne, setStateOne] = useState({
    value: ''
  });

  const [stateTwo, setStateTwo] = useState({
    dirty: false,
    errorText: stateTwoDefaultErrorText,
    helpText: stateTwoDefaultHelpText,
    invalid: undefined,
    label: 'Type "peter"',
    name: 'simple-validation-example-peter',
    value: ''
  });

  const [stateThree, setStateThree] = useState({
    value: ''
  });

  const [stateFour, setStateFour] = useState({
    value: ''
  });

  const [stateFive, setStateFive] = useState({
    type: 'text',
    value: ''
  });

  const [stateSix, setStateSix] = useState({
    dirty: false,
    errorText: 'Error: Please enter "john"',
    helpText: stateSixDefaultHelpText,
    invalid: false,
    label: 'Simple Validation (type "john")',
    name: 'simple-validation-example',
    value: ''
  });

  const [stateSeven, setStateSeven] = useState({
    dirty: false,
    errorText: '"Error: at least 8 characters, - at least 1 uppercase, lowercase and number, - can contain special characters"',
    helpText: stateSevenDefaultHelpText,
    invalid: false,
    label: '',
    name: '',
    pattern: '^(?=.*)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$',
    type: 'password',
    value: ''
  });

  const [stateEight, setStateEight] = useState({
    errorText: '',
    invalid: undefined,
    showCheckmark: true,
    value: 'Mango'
  });

  const submitStateTwo = e => {
    const invalid = stateTwo.value !== "peter";

    if (invalid) {
      inputTwoRef.current.focus();
    }

    setStateTwo({
      ...stateTwo,
      helpText: !invalid ? 'You\'ve successfully typed "peter"' : stateTwoDefaultHelpText,
      errorText: stateTwo.value.length > 5 ? 'Error: Too many characters' : stateTwoDefaultErrorText,
      invalid: invalid
    });
  };

  const resetStateTwo = () => {
    setStateTwo({
      ...stateTwo,
      dirty: false,
      helpText: stateTwoDefaultHelpText,
      errorText: stateTwoDefaultErrorText,
      invalid: false,
      value: ''
    });
  };

  const handleStateThree = value => {
    setStateThree({ value });
  };

  const handleStateFour = value => {
    setStateFour({ value });
  };

  const submitStateSix = e => {
    const invalid = stateSix.value !== "john";

    if (invalid) {
      inputSixRef.current.focus();
    }

    setStateSix({
      ...stateSix,
      helpText: !invalid ? 'You\'ve successfully entered "john"' : stateSixDefaultHelpText,
      invalid: invalid
    });
  };

  const resetStateSix = () => {
    setStateSix({
      ...stateSix,
      dirty: false,
      helpText: stateSixDefaultHelpText,
      invalid: false,
      value: ''
    });
  };

  const submitStateSeven = () => {
    const pattern = new RegExp(stateSeven.pattern);
    const invalid = !pattern.test(stateSeven.value);

    if (invalid) {
      inputSevenRef.current.focus();
    }

    setStateSeven({
      ...stateSeven,
      helpText: !invalid ? 'Successfully entered a valid password' : stateSevenDefaultHelpText,
      invalid: invalid
    });
  };

  const resetStateSeven = () => {
    setStateSeven({
      ...stateSeven,
      dirty: false,
      helpText: stateSevenDefaultHelpText,
      invalid: false,
      value: ''
    });
  };

  const html5PatternDefaultHelpText = 'Enter 2 to 5 characters';
  const html5PatternSuccessHelpText = "You've successfully entered 2 to 5 characters";
  const [html5PatternHelpText, setHtml5PatternHelpText] = useState(html5PatternDefaultHelpText);

  const handleHtml5PatternChange = e => {
    setHtml5PatternHelpText(e.target.checkValidity() ? html5PatternSuccessHelpText : html5PatternDefaultHelpText);
  };

  const html5EmailDefaultHelpText = 'Enter a valid email';
  const html5EmailSuccessHelpText = "You've successfully entered a valid email";
  const [html5EmailHelpText, setHtml5EmailHelpText] = useState(html5PatternDefaultHelpText);

  const handleHtml5EmailChange = e => {
    setHtml5EmailHelpText(e.target.checkValidity() ? html5EmailSuccessHelpText : html5EmailDefaultHelpText);
  };

  const validateFruit = value => setStateEight({
    ...stateEight,
    errorText: value === 'Citron' ? 'Error: Invalid fruit' : value === '' ? 'Error: Field cannot be empty' : ''
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">
            Input
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Alert position="relative" alertType="information">
            <Grid>
              <Row>
                <Col>
                  <AlertIcon>
                    <Icon name="info" resolution="low" />
                  </AlertIcon>
                  <AlertLabel>
                    <AlertLabelTitle>
                      All native React input props apply e.g. defaultValue, value... etc
                    </AlertLabelTitle>
                    The Input component can be used in an uncontrolled or
                    controlled way with the presence of &quot;defaultValue&quot; or
                    &quot;value&quot; props as described in the React docs.
                  </AlertLabel>
                </Col>
              </Row>
            </Grid>
          </Alert>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">
            Input Text Default (uncontrolled) example
          </Typography>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text Default (controlled) example</Typography>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            onChange={e => setStateOne({ value: e.target.value })}
            showHelpText
            value={stateOne.value}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text ReadOnly</Typography>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            readOnly
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text Disabled</Typography>
          <Input
            disabled
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text Pre-filled</Typography>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            showHelpText
            defaultValue="John Smith"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Divider type="standard" />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input with Icons</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text with Custom Button Icon (clear)</Typography>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            onChange={e => handleStateThree(e.target.value)}
            ref={inputThreeRef}
            showHelpText
            value={stateThree.value}
          >
            <InputOption>
              <ButtonIcon
                ariaLabel="clear"
                iconType="light-tiny"
                onClick={() => {
                  inputThreeRef.current.focus();
                  setTimeout(() => {
                    handleStateThree('');
                  });
                }}
              >
                <Icon name="close" resolution="tiny" />
              </ButtonIcon>
            </InputOption>
          </Input>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text with Custom Button Icon on the left</Typography>
          <Input
            contentBeforeInput={<InputOption>
              <ButtonIcon
                ariaLabel="Account"
                iconType="light-tiny"
              >
                <Icon name="account" resolution="low" />
              </ButtonIcon>
            </InputOption>}
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">
            Input Text with Custom Button Icon (clear)
            that appears after value is entered
          </Typography>
          <Input
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            onChange={e => handleStateFour(e.target.value)}
            ref={inputFourRef}
            showHelpText
            value={stateFour.value}
          >
            {
              stateFour.value.length
                ? <InputOption>
                  <ButtonIcon
                    ariaLabel="clear"
                    iconType="light-tiny"
                    onClick={() => {
                      inputFourRef.current.focus();
                      setTimeout(() => {
                        handleStateFour('');
                      });
                    }}
                  >
                    <Icon name="close" resolution="tiny" />
                  </ButtonIcon>
                </InputOption>
                : undefined
            }
          </Input>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text with Custom Button Icon (toggle password view)</Typography>
          <Input
            helpText="Please enter your password"
            label="Password"
            name="password"
            onChange={e => setStateFive({
              ...stateFive,
              value: e.target.value
            })}
            showHelpText
            type={stateFive.type}
            value={stateFive.value}
          >
            <InputOption>
              <ButtonIcon
                ariaLabel={stateFive.type === 'text' ? 'hide password' : 'show password'}
                iconType="light-tiny"
                onClick={() => setStateFive({
                  ...stateFive,
                  type: stateFive.type === 'text' ? 'password' : 'text'
                })}
              >
                {
                  stateFive.type === 'text'
                    ? <Icon name="password-hide" resolution="tiny" />
                    : <Icon name="password-show" resolution="tiny" />
                }
              </ButtonIcon>
            </InputOption>
          </Input>
        </Col>
      </Row>
      <Row>
        <Col>
          <Divider type="standard" />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input with Native Validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text Default HTML5 Required</Typography>
          <Input
            errorText="Error: Please fill out this field"
            helpText="Please enter your full name"
            label="Full Name"
            name="full-name"
            required
            showErrorText
            showHelpText
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text with internal HTML5 regex pattern validation</Typography>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText="Error: Enter 2 - 5 characters"
            onChange={handleHtml5PatternChange}
            helpText={html5PatternHelpText}
            label="Enter 2 to 5 characters starting with a letter e.g. abc or a2d"
            name="default-username"
            pattern="^[a-zA-Z][a-zA-Z0-9-_\.]{1,4}$"
            placeholder="2 to 5 characters"
            required
            showCheckmark
            showErrorText
            showHelpText
            type="text"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text Default with HTML5 &quot;type=email&quot; Validation</Typography>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText="Error: Invalid email address"
            onChange={handleHtml5EmailChange}
            helpText={html5EmailHelpText}
            label="Email Address"
            name="email-validation"
            placeholder="email@visa.com"
            showCheckmark
            showErrorText
            showHelpText
            type="email"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Divider type="standard" />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Input with Custom Validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text noValidate (HTML5)</Typography>
          <Typography>
            Disable native HTML5 validation with noValidate
            prop and manage your own validations
          </Typography>
          <Input
            errorText={stateTwo.errorText}
            helpText={stateTwo.helpText}
            invalid={stateTwo.invalid}
            label={stateTwo.label}
            name={stateTwo.name}
            noValidate
            onChange={e => setStateTwo({
              ...stateTwo,
              dirty: false,
              value: e.target.value
            })}
            onKeyUp={e => {
              if (e.which === 13) {
                submitStateTwo();
              }
            }}
            ref={inputTwoRef}
            required
            showErrorText
            showHelpText
            value={stateTwo.value}
          />
          <br />
          <ButtonText colorScheme="secondary" onClick={resetStateTwo}>Reset</ButtonText>
          <ButtonText onClick={submitStateTwo}>Validate</ButtonText>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Custom Input Simple Validation</Typography>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText={stateSix.errorText}
            helpText={stateSix.helpText}
            invalid={stateSix.invalid}
            label={stateSix.label}
            name={stateSix.name}
            noValidate
            onChange={e => { setStateSix({
              ...stateSix,
              dirty: true,
              value: e.target.value
            })}}
            onKeyUp={e => {
              if (e.which === 13) {
                submitStateSix();
              }
            }}
            ref={inputSixRef}
            showCheckmark={stateSix.dirty && !stateSix.invalid}
            showErrorText
            showHelpText
            type="text"
            value={stateSix.value}
          />
          <br />
          <ButtonText colorScheme="secondary" onClick={resetStateSix}>Reset</ButtonText>
          <ButtonText onClick={submitStateSix}>Validate</ButtonText>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h3">Input Text with Custom Button Icon (toggle password view) with HTML5 pattern Validation</Typography>
          <List>
            <li>at least 8 characters</li>
            <li>must contain at least 1 uppercase letter</li>
            <li>must contain at least 1 lowercase letter</li>
            <li>must contain at least 1 number</li>
            <li>option: can contain special characters</li>
          </List>
          <Typography>example: Testing123!</Typography>
          <Input
            containerProps={{
              style: {
                width: '350px'
              }
            }}
            errorText={stateSeven.errorText}
            helpText={stateSeven.helpText}
            invalid={stateSeven.invalid}
            label="Password"
            name="password"
            noValidate
            onChange={e => setStateSeven({
              ...stateSeven,
              dirty: true,
              value: e.target.value
            })}
            onKeyUp={e => {
              if (e.which === 13) {
                submitStateSeven();
              }
            }}
            pattern={stateSeven.pattern}
            ref={inputSevenRef}
            required
            showErrorText
            showHelpText
            type={stateSeven.type}
            value={stateSeven.value}
          >
            <InputOption>
              <ButtonIcon
                ariaLabel={stateSeven.type === 'text' ? 'hide password' : 'show password'}
                iconType="light-tiny"
                onClick={() => setStateSeven({
                  ...stateSeven,
                  type: stateSeven.type === 'text' ? 'password' : 'text' }
                )}
              >
                {
                  stateSeven.type === 'text'
                    ? <Icon name="password-hide" resolution="tiny" />
                    : <Icon name="password-show" resolution="tiny" />
                }
              </ButtonIcon>
            </InputOption>
          </Input>
          <br />
          <ButtonText colorScheme="secondary" onClick={resetStateSeven}>Reset</ButtonText>
          <ButtonText onClick={submitStateSeven}>Validate</ButtonText>
        </Col>
      </Row>
      <Row>
        <Col>
          <Divider type="standard" />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Clear and reset value</Typography>
          <div style={{ display: 'inline-block', marginBottom: '20px' }}>
            <Input
              errorText={stateEight.errorText}
              helpText="Please enter a fruit"
              id="default-input-example"
              invalid={stateEight.invalid}
              label="Fruit"
              name="default-fruit"
              onBlur={e => validateFruit(e.target.value)}
              onChange={e => setStateEight({ value: e.target.value })}
              showCheckmark={stateEight.showCheckmark}
              showErrorText
              type="text"
              value={stateEight.value}
            />
            <ButtonGroup direction="right" style={{ marginTop: '30px' }}>
              <ButtonText
                colorScheme="secondary"
                onClick={() => setStateEight({
                  ...stateEight,
                  errorText: 'Error: Field cannot be empty',
                  invalid: true,
                  value: ''
                })}
              >
                Clear
              </ButtonText>
              <ButtonText
                colorScheme="secondary"
                onClick={() => setStateEight({
                  ...stateEight,
                  errorText: '',
                  invalid: false,
                  showCheckmark: false,
                  value: 'Mango'
                })}
              >
                Reset
              </ButtonText>
              <ButtonText
                onClick={() => setStateEight({
                  ...stateEight,
                  errorText: 'Error: Invalid fruit',
                  invalid: true,
                  value: 'Citron'
                })}
              >
                Invalid Fruit
              </ButtonText>
              <ButtonText
                colorScheme="secondary"
                onClick={() => setStateEight({
                  ...stateEight,
                  invalid: false,
                  showCheckmark: !stateEight.showCheckmark
                })}
              >
                Toggle Checkmark
              </ButtonText>
            </ButtonGroup>
          </div>
        </Col>
      </Row>
    </Grid>
  );
};

export default InputExample;
