import React from 'react';
import { oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-divider';

const Divider = ({ className, title, type, ...remainingProps }) => (
  <hr
    aria-hidden="true"
    className={classnames({
      [`${CSS_PREFIX}--${type}`]: Boolean(type)
    }, className)}
    title={title}
    {...remainingProps}
  />
);

Divider.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
   * Title
   */
  title: string,

  /**
   * Type
   */
  type: oneOf(['section', 'standard'])
};

Divider.defaultProps = {
  title: 'Separator',
  type: 'standard'
};

Divider.displayName = 'Divider';

export default Divider;
